import type { KVNamespace, EventContext } from '@cloudflare/workers-types';

interface Env {
    data_cache: KVNamespace
}

const memoKV = {
    store: {} as Record<string, string>,
    put(key: string, value: string):Promise<void> {
        const store = memoKV.store;
        store[key] = value;
        memoKV.store = store;
        return Promise.resolve()
    },
    get(key: string):Promise<string> {
        const store = memoKV.store;
        const value = store[key];
        if (value) {
            return Promise.resolve(value)
        }
        return Promise.resolve('')
    },
    delete(key: string):Promise<void>  {
        const store = memoKV.store;
        delete store[key];
        memoKV.store = store;
        return Promise.resolve()
    }
}

let ctx:EventContext<Env, any, any>;

export function initStore(context: EventContext<Env, any, any>){
    ctx = context
}


export function getStore(){
    console.log('ctx',ctx)
    if (ctx) {
        console.info('from ctx', ctx.env.data_cache)
        return ctx.env.data_cache
    }
    return memoKV
}