import { fetchRequestHandler } from '@trpc/server/adapters/fetch';
import { appRouter } from './router';
import { initStore } from '../service/store';
import { EventContext } from '@cloudflare/workers-types';
export default {
  async fetch(request: Request, _env: Record<string, any>, ctx: EventContext<any, any, any>): Promise<Response> {
    initStore(ctx);
    console.log(ctx.env.data_cache,'cache');
    return fetchRequestHandler({
      endpoint: '/trpc',
      req: request,
      router: appRouter,
    });
  },
};
